[type="radio"]:not(:checked), [type="radio"]:checked {
    position: relative;
    opacity: 1;
    pointer-events: auto;
}

[type="checkbox"], [type="radio"] {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding: 0;
}

label {
    font-size: 1rem;
    color: #333;
}

button{
     border-radius: 15px;
     background-color: #fff;
     height: 32px;
     margin: 0 6px;
 }

button:focus {
    outline: none;
    background-color: #fff;
}